import axios from 'axios';
import React, { useState } from 'react';

function Contact() {
  const[name,setName]=useState('');
  const[email,setEmail]=useState('');
  const[message,setMessage]=useState('');
 
  const handleSubmmit=async(e)=>{
    e.preventDefault();
    try {
      const { data } = await axios.post(
        "http://localhost:5000/api/user/contact",
        { name, email, message },
        {
          headers: {
            "Content-Type": "application/json",
            // "Authorization":`Bearer ${token}`,
          },
        }
      );
      alert(data);
    } catch (error) {
      console.log(error);
    }
  }
  return (
    <div className="flex flex-col h-screen">
      {/* Sidebar */}
      <div className="flex-grow md:flex md:flex-row">
        <div className="bg-gray-900 text-white w-full md:w-1/4 p-10">
          <p className="mt-4 text-sm leading-7 font-regular uppercase">Contact</p>
          <h3 className="text-3xl sm:text-4xl leading-normal font-extrabold tracking-tight">
            Get In <span className="text-indigo-600">Touch</span>
          </h3>
          <p className="text-lg mt-6">We'd love to hear from you. Reach out to us anytime.</p>
          
          <div className="flex items-center mt-5">
            <svg 
              className="h-6 mr-2 text-indigo-600" 
              fill="currentColor" 
              xmlns="http://www.w3.org/2000/svg" 
              viewBox="0 0 489.536 489.536"
              xmlnsXlink="http://www.w3.org/1999/xlink" 
              enableBackground="new 0 0 489.536 489.536" 
              alt="Location Icon"
            >
              <g>
                <g>
                  <path
                    d="m488.554,476l-99-280.2c-1-4.2-5.2-7.3-9.4-7.3h-45.6c12.9-31.1 19.6-54.9 19.6-70.8 0-64.6-50-117.7-112.5-117.7-61.5,0-112.5,52.1-112.5,117.7 0,17.6 8.2,43.1 19.9,70.8h-39.7c-4.2,0-8.3,3.1-9.4,7.3l-99,280.2c-3.2,10.3 6.3,13.5 9.4,13.5h468.8c4.2,0.5 12.5-4.2 9.4-13.5zm-246.9-455.3c51,1.06581e-14 91.7,43.7 91.7,96.9 0,56.5-79.2,182.3-91.7,203.1-31.3-53.1-91.7-161.5-91.7-203.1 0-53.1 40.6-96.9 91.7-96.9zm-216.7,448l91.7-259.4h41.7c29.9,64.1 83.3,151 83.3,151s81.4-145.7 83.8-151h47.4l91.7,259.4h-439.6z"
                  ></path>
                  <rect width="136.5" x="177.054" y="379.1" height="20.8"></rect>
                  <path
                    d="m289.554,108.2c0-26-21.9-47.9-47.9-47.9s-47.9,21.9-47.9,47.9 20.8,47.9 47.9,47.9c27.1,0 47.9-21.8 47.9-47.9zm-75-1c0-14.6 11.5-27.1 27.1-27.1s27.1,12.5 27.1,27.1-11.5,27.1-27.1,27.1c-14.6,2.84217e-14-27.1-12.5-27.1-27.1z"
                  ></path>
                </g>
              </g>
            </svg>
            <span className="text-sm">House #14, Street #12, Darulaman Road, Kabul, Afghanistan.</span>
          </div>
        </div>
        
        <div className="md:col-span-8 p-10 flex-grow bg-gray-100">
          <form  onSubmit={handleSubmmit}
            action="#" 
            method="POST" 
            className="space-y-6 max-w-2xl mx-auto bg-white p-8 rounded-lg shadow-lg"
          >
            <h2 className="text-3xl font-extrabold text-gray-900 mb-8 text-center">Contact Us</h2>
            
            <div>
              <label htmlFor="name" className="block text-lg font-medium text-gray-700">Your Name</label>
              <input
                type="text"
                name="name"
                id="name"
                className="mt-2 block w-full border-gray-300 shadow-sm focus:ring-indigo-500 focus:border-indigo-500 sm:text-lg rounded-lg p-3"
                required
                onChange={(e)=>setName(e.target.value)}
              />
            </div>

            <div>
              <label htmlFor="email" className="block text-lg font-medium text-gray-700">Your Email</label>
              <input
                type="email"
                name="email"
                id="email"
                className="mt-2 block w-full border-gray-300 shadow-sm focus:ring-indigo-500 focus:border-indigo-500 sm:text-lg rounded-lg p-3"
                required
                onChange={(e)=>setEmail(e.target.value)}

              />
            </div>

            <div>
              <label htmlFor="message" className="block text-lg font-medium text-gray-700">Your Message</label>
              <textarea
                name="message"
                id="message"
                rows="4"
                className="mt-2 block w-full border-gray-300 shadow-sm focus:ring-indigo-500 focus:border-indigo-500 sm:text-lg rounded-lg p-3"
                required
                onChange={(e)=>setMessage(e.target.value)}


              />
            </div>

            <div className="mt-6">
              <button
                type="submit"
                className="w-full py-3 px-6 bg-indigo-600 text-white font-semibold rounded-lg shadow-md hover:bg-indigo-700 focus:ring-2 focus:ring-indigo-500 focus:ring-opacity-50 transition duration-300"
              >
                Send Message
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default Contact;
